<template>
  <div id="createArticle">
    <div class="Article-wrapper">
      <!--  -->
      <el-input placeholder="" v-model="articleName">
        <template slot="prepend"> </template>
      </el-input>
      <!--  -->

      <el-input
        placeholder=""
        v-model="articleTags"
      >
        <template slot="prepend"></template>
      </el-input>
      <!--  -->
      <div class="articleLocation_wrapper">
        <span></span>
        <div class="select_wrapper " id="select_hp">
          <select name="" id="" @change="getElement">
            <option index="-1"></option>
            <option
              :value="JSON.stringify(value.childrens)"
              v-for="(value, index) in directorys"
              :key="index"
              :index="index"
              :path="value.path"
              :id="value.id"
              >{{ value.name }}</option
            >
          </select>
        </div>
      </div>
      <!--  -->
      <div class="wangeEdit_wrapper">
        <mark-down v-model="articleContent" @success="getArticle"></mark-down>
      </div>
      <!--  -->
      <div class="btn_wrapper">
        <el-button-group>
          <el-button
            type="primary"
            icon="el-icon-circle-close"
            @click="cancleEdit"
            ></el-button
          >
          <el-button
            type="primary"
            icon="el-icon-upload2"
            @click="commitArticle"
            ></el-button
          >
        </el-button-group>
      </div>
    </div>
  </div>
</template>
<script>
import WangeEdit from "@/components/other/WangEditor";
import MarkDown from "@/components/other/MarkDown";

import {
  getDirectory,
  updateDirectory,
  putArticle,
  httpArticleAdd,
} from "@/network/Article.js";

import { httpCreateDirector, httpGetDirectory } from "@/network/Directory.js";

import { validateLogin } from "@/utils/Validate";

export default {
  components: {
    WangeEdit,
    "mark-down": MarkDown,
  },
  data() {
    return {
      articleName: "",
      articleContent: "",
      articleHtml: "",
      directorys: [],
      articleTags: "",
    };
  },
  created() {
    if (validateLogin() === false) this.$router.push("/");
    httpGetDirectory(this.$store.state.people.user.id).then((data) => {
      this.directorys = data.data;
    });
  },
  beforeDestroy() {},
  methods: {
    /**
     * 
     */
    cancleEdit() {
      //this.$router.push("/");
      window.history.back();
    },
    /**
     * 
     */
    commitArticle(e) {
      /** */
      let articleName = this.articleName;
      if (articleName.trim() === "") {
        this.$message({
          message: '<span style="color:red"></span>',
          dangerouslyUseHTMLString: true,
        });
        return;
      }

      /** */
      let selecttions = document.getElementById("select_hp").childNodes; // 
      let arrays = [];
      for (let i = 0; i < selecttions.length; i++) {
        arrays.push([i, selecttions[i].selectedIndex]);
      }
      let temp = arrays.pop();
      let isRootTree = false; //
      try {
        while (temp[1] == 0) {
          temp = arrays.pop();
        }
      } catch (error) {
        // 
        isRootTree = true;
        this.directorys.length === undefined
          ? this.$message({
              dangerouslyUseHTMLString: true,
              message:
                '<strong></strong><strong></strong><span style="color:red"></span>',
            })
          : this.$message({
              message: '<span style="color:red"></span>',
              dangerouslyUseHTMLString: true,
            });
        return;
      }
      let option = isRootTree ? null : selecttions[temp[0]][temp[1]]; // option 

      let pid = isRootTree
        ? this.$store.state.people.user.id
        : option.getAttribute("id");
      let path = isRootTree
        ? `/${this.$store.state.people.user.id}`
        : option.getAttribute("path");
      let tags = this.articleTags;
      let articleId;
      /**   */
      httpArticleAdd(
        pid,
        articleName,
        this.articleContent,
        this.$store.state.people.user.userName,
        tags,
        this.$store.state.people.user.id,
        this.articleHtml
      )
        .then((data) => {
          articleId = data.insertId;
          return httpCreateDirector(
            pid,
            path,
            articleName,
            articleId,
            this.$store.state.people.user.id
          );
        })
        .then((Response) => {
          this.$router.push({
            path: `/Home/ReadArticle/${articleId}`,
          });
        })
        .catch((err) => {
          // console.log("");
        });
    },
    /**
     * 
     */
    getElement(e) {
      const option = e.target.options[e.target.selectedIndex];
      this.deleteSelection(option);
      const value = option.getAttribute("value");
      // optionvaluecreate
      if (value === "create") {
        this.createDilog(e);
      }
      // value
      else if (value) {
        this.createSelection(value, option);
      }
      //this.createSelection(msg,option);
    },
    /**
     * msg smgselectionoption
     * documentObj document
     * (msg[msg.length-1].length>0)selection
     */
    createSelection(msg, documentObj) {
      //  
      msg = JSON.parse(msg);
      if (msg[msg.length - 1].length === 0) return;

      const parent = documentObj.parentNode.parentNode; // selection
      const selectObj = document.createElement("select");
      const _this = this;
      selectObj.addEventListener("change", _this.getElement, false);

      // option
      let option = document.createElement("option");
      option.innerHTML = "";
      option.setAttribute("index", "-1");
      selectObj.appendChild(option);

      try {
        // ""otion
        for (let obj of msg) {
          // obj0
          if (obj.length > 0) {
            for (let index in obj) {
              const option = document.createElement("option");
              option.setAttribute(
                "value",
                JSON.stringify(obj[index].childrens)
              );
              option.setAttribute("index", index); // index 
              option.setAttribute("path", obj[index].path);
              option.setAttribute("id", obj[index].id);
              option.innerHTML = obj[index].name;
              selectObj.appendChild(option);
            }
          }
        }
      } catch (error) {}

      // ""otion
      option = document.createElement("option");
      option.setAttribute("value", "create");
      option.innerText = "";
      //selectObj.appendChild(option); //

      // seleciton
      parent.appendChild(selectObj);
    },
    /**
     * selection
     * */

    deleteSelection(documentObj) {
      const parent = documentObj.parentNode.parentNode;
      let nextDocObj = documentObj.parentNode.nextElementSibling;
      let deletArray = [];

      //    
      while (nextDocObj) {
        deletArray.push(nextDocObj);
        nextDocObj = nextDocObj.nextElementSibling;
      }
      for (const iterator of deletArray) {
        parent.removeChild(iterator);
      }
    },
    /**
     * 
     */
    createDilog(event) {
      //  
      var hpDialog = document.getElementById("hpDialog");
      hpDialog && document.body.removeChild(hpDialog);

      // 
      const divObj = document.createElement("div");
      divObj.setAttribute(
        "style",
        `
        position: fixed;
        background: white;
        box-shadow: 0px 0px 10px 10px rgba(146, 146, 146);
        top:calc(50% - 100px);
        left:calc(50% - 200px);
        width:400px;
        height:200px;
        display: flex;
        padding: 15px;
        flex-direction: column;
        justify-content: space-around;
        z-index:999999;
      `
      );
      divObj.setAttribute("id", "hpDialog");

      //
      const input = document.createElement("input");
      input.setAttribute("placeholder", "");
      input.setAttribute("class", "g_input");
      divObj.appendChild(input);

      //
      const btnGroup = document.createElement("div");
      btnGroup.setAttribute(
        "style",
        `
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        `
      );

      //
      const buttonDo = document.createElement("button");
      buttonDo.setAttribute("class", "g_btn g_btn_larger g_btn_success");
      buttonDo.innerText = "";
      buttonDo.addEventListener(
        "click",
        () => {
          if (input.value.trim() === "") {
            alert("!");
            return;
          }
          document.body.removeChild(divObj);
          const option = event.target.options[event.target.selectedIndex]; // option
          const optionWrapper = event.target; // option option

          //optionoptionselection
          const newOption = document.createElement("option");
          newOption.setAttribute("value", input.value);
          newOption.setAttribute("index", "-2");
          newOption.innerText = input.value;
          optionWrapper.insertBefore(newOption, option);
          optionWrapper.selectedIndex--; // -1option
          this.getElement(event);
        },
        false
      );

      //
      const buttonCancle = document.createElement("button");
      buttonCancle.setAttribute("class", "g_btn g_btn_larger g_btn_cancle");
      buttonCancle.innerText = "";
      buttonCancle.addEventListener(
        "click",
        () => {
          document.body.removeChild(divObj);
          event.target.selectedIndex = 0;
        },
        false
      );
      btnGroup.appendChild(buttonDo);
      btnGroup.appendChild(buttonCancle);

      divObj.appendChild(btnGroup);

      document.body.appendChild(divObj);
    },
    /**
     * 
     */
    async getDirectory() {
      await getDirectory("/Directory/getAllDirectory")
        .then((Response) => {
          this.directorys = Response.data;
        })
        .catch((err) => {
          //console.log(err);
        });
    },
    /** */
    getArticle(event) {
      this.articleHtml = event.articleHtml;
    },
  },
};
</script>
<style lang="less">
.editer-modle {
  background: white;
}
#createArticle {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(146, 146, 146, 0.3);
  .Article-wrapper {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    box-shadow: 0px 0px 10px 10px rgb(146, 146, 144);
    background: white;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    .input_wrapper {
      display: block;
      padding: 10px;
      outline: none;
      border: 0;
      border-bottom: 1px solid rgba(146, 146, 146, 0.8);
      height: 18px;
      flex: none;
    }
    .articleLocation_wrapper {
      height: 40px;
      line-height: 40px;
      padding: 0 10px;
      display: flex;
      flex-direction: row;
      justify-content: start;
      border-bottom: 1px solid rgba(146, 146, 146, 0.8);
    }
    .wangeEdit_wrapper {
      flex: 1;
      min-height: 200px;
      overflow: scroll;
    }
    .article_tags {
      width: 100%;
      flex: none;
    }
    .btn_wrapper {
      flex: none;
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      height: 60px;
      justify-content: center;
      align-items: center;
    }
  }
}
</style>
