import instance from './base.js'

/**
 * 
 * @param {String} url 
 * promise
 */
export function getDirectory(url) {
    return instance.get(url);
}
/**
 * 
 * @param {String} url 
 * @param {Array} params 
 * @param {Number} id     ID
 * @param {String} path   
 * @param {String} name   
 * @param {Number} flag   0,
 */
export function updateDirectory(url,params) {
    let {id,path,name} =  params;
    //return instance.post(url,id,path,name);
    instance.post(url,params);
}

/**
 * 
 * @param {String} url 
 * @param {String} articleId  ID
 * @param {String} articleName  
 * @param {String} articleContent 
 */
export function putArticle(url,articleId,articleName,articleContent) {
    return instance.post(url,articleId,articleName,articleContent);
}

/**
 * 
 * @param {String} url 
 * @param {Array} params 
 * @param {Number} pid  ID
 * @param {String} path 
 * @param {String} name 
 * @param {Number} articleId 0id;
 */
export function createDirector(url,params) {
    return instance.post(url,params);
}

/**
 * 
 */
export function deleteDirector(url,params) {
    return instance.post(url,params);
}

/**
 * 
 * @param {Number} pid  ID
 * @param {String} path 
 * @param {String} name 
 * @param {Number} articleId 0id;
 * @param {Number} uid id;
 */
 export function httpCreateDirector(pid,path,name,articleId,uid) {
   let params = {pid,path,name,articleId,uid}
   return instance.post(
       `/directory/add`,
       params);
}

/**
 * 
 * @param {Number} id   ID
 */
 export function httpDeleteDirector({id,uid}) {
    return instance.request({
        url: '/director/delete',
        method: 'get', // default
        params: {
            id,
            uid,
        },
    })
}

/**
 * 
 * @param {Number} id     ID
 * @param {String} path   
 * @param {String} name   
 */
 export function httpUpdateDirectory(id,path,name) {
    return instance.post(
        '/directory/update',
        {id,path,name}
    );
}

/**
 * 
 * @param {number} uid id
 * promise
 */
 export function httpGetDirectory(uid) {
    return instance.get(
        `/directory/queryAll?uid=${uid}`
    );
}